@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,900;1,800&display=swap');

$redText:#FB2E55;

html, body{
    margin:auto;
    padding:auto;
    font-family:'Poppins', sans-serif;
}

h1{
    font-size:3.5rem;
    font-weight:800;
}

h2{
    font-size:1.7rem;
    font-weight:600;
}

h3{
    font-size:1.2rem;
    font-weight:500;
}

h4{
    font-size:0.95rem;
    font-weight:400;
    
}
h1, h2, h3, h4, p {
    margin:0;
    padding:0;
}