@import url("../../__variables.scss");
html, body {
  padding: 0;
  margin: 0;
}

@media only screen and (max-width: 468px) {
  .profitBanner {
    background: #FB2E55;
    width: 100%;
    height: 100%;
    padding: 30px 0px;
  }
  .profitBanner .container {
    width: 90%;
    margin-left: 5%;
  }
  .profitBanner .container h2 {
    margin: 0;
    padding: 0;
    color: white;
    text-align: center;
  }
  .profitBanner .container h2 button {
    background: #010F23;
    color: white;
    border: none;
    color: white;
    font-size: 1.2rem;
    font-weight: 500;
    width: 65%;
    padding: 12px;
    border-style: none;
    border-radius: 5px;
    margin-top: 25px;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.7), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.7), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  .profitBanner .container h2 .alt {
    background: #D7DADF;
    color: #010F23;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.7), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.7), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
  .profitBanner .container img {
    width: 50%;
    margin-left: 25%;
    margin-top: 25px;
    display: none;
  }
}
