@media only screen and (min-width: 480px) {
  .header {
    position: fixed;
    z-index: 999;
    background: transparent;
    width: 100%;
    height: 60px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
  .header nav {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
  }
  .header nav ul {
    list-style: none;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
  .header nav ul li {
    color: white;
    font-family: 600;
    font-size: 1.6rem;
  }
}

@media only screen and (max-width: 486px) {
  .header {
    position: fixed;
    z-index: 999;
    background: #FB2E55;
    width: 100%;
    height: 60px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
  .header .responsive_nav {
    -webkit-transform: translate(0);
            transform: translate(0);
  }
  .header nav {
    height: 100vh;
    width: 100%;
    background-color: #FB2E55;
    position: relative;
    -webkit-transform: translate(0, -100vh);
            transform: translate(0, -100vh);
    -webkit-transition: 0.5s;
    transition: 0.5s;
  }
  .header nav ul {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-60%, -65%);
            transform: translate(-60%, -65%);
  }
  .header nav ul li {
    color: white;
    list-style-type: none;
    list-style-position: inside;
    font-size: 1.5rem;
    padding: 1rem;
  }
  .header .bars {
    color: white;
    font-size: 1.8rem;
    font-weight: 800;
    position: fixed;
    top: 15px;
    right: 15px;
  }
}
