@media only screen and (min-width:480px){

    .header{
        position: fixed;
        z-index:999;
        background:transparent;
        width:100%;
        height:60px;
        display:flex;
        flex-wrap: wrap;
    
        nav{
            display:flex;
            width:100%;
    
            ul{
                list-style: none;   
                display: flex;
                width:100%;
                flex-direction: row;
                flex-wrap: wrap;
    
                li{
                    color:white;
                    font-family:600;
                    font-size:1.6rem;
                }
            }
        }
    }
}



@media only screen and (max-width:486px){
    .header{
        position: fixed;
        z-index:999;
        background:#FB2E55;
        width:100%;
        height:60px;
        display:flex;
        flex-wrap: wrap;
    
        .responsive_nav{
            transform:translate(0);
        }

        nav{
            height:100vh;
            width:100%;
            background-color:#FB2E55;
            position: relative;
            transform: translate(0,-100vh);
            transition:0.5s;

        ul{
            position: absolute;
            top:50%;
            left:50%;
            transform: translate(-60%, -65%);

            li{
                color:white;
                list-style-type: none;
                list-style-position:inside;
                font-size:1.5rem;
                padding:1rem;
            }
        }
    }

    

    .bars{
        color:white;
        font-size:1.8rem;
        font-weight:800;
        position: fixed;
        top:15px;
        right:15px;
    }
}}