@import url('../../__variables.scss');

$redText:#FB2E55;

.homepage{
    height:100vh;
    width:100%;
    background-image:url('../../background-image.png');
    background-size:cover;
    background-position: center;
    z-index:0;

    .container{
        width:70%;
        margin-left:15%;
        margin-right:15%;
        height:100%;

        .row{
            width:100%;
            height:100%;
            justify-content: center;
            align-items: center;
                        
            .col-50{
                width:50%;
                float:left;
            }

            .arrow-down{
                color:white;
                font-size:2rem;
                width:10%;
                margin-left:45%;
            }
        }
    }
}

//phone view

@media only screen and (max-width:468px){
    
    .call{
        position: fixed;
        z-index:999;
        right:20px;
        bottom:20px;
        width:35%;
        padding:8px 0px;
        border-radius:10px;
        border:none;
        box-shadow:0 4px 8px 0 rgba(0, 0, 0, 0.7), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        background:#010F23;
        font-size:1.2rem;
        font-weight:600;
        a{
            color:#ffffff;
            text-decoration: none;
        }
    }
    .homepage{
        width:100%;
        background-image:url('../../background-image.png');
        background-size:cover;
        background-position: 600px;
        z-index:0;
        overflow:hidden;
        padding-top:13vh;
        margin-bottom:0;
        
        .container{
            width:90%;
            margin-left:5%;
            height:100%;
    
            .row{
                width:100%;
                height:100%;
    
                .col-50{
                    width:100%;
                    background-size:red;

                    img{
                        width:100%;
                        margin-top:10%;
                    }

                    h1{
                        color:$redText;
                        margin-bottom:0px;

                        span{
                            color:white;
                        }
                    }

                    h3{
                        color:white;
                        margin-top:0px;

                    }

                    .purchase{
                        background-image:linear-gradient(to top right,#FD086D,#FB3153,#FA5A3A);
                        color:white;
                        font-size:1.2rem;
                        font-weight:500;
                        width:65%;
                        padding:9px;
                        margin-top:2vh;
                        border-style:none;
                        border-radius:5px;
                        box-shadow:0 4px 8px 0 rgba(0, 0, 0, 0.7), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

                    }
                }
            }
        }
    }
}