@import url("../../__variables.scss");
.homepage {
  height: 100vh;
  width: 100%;
  background-image: url("../../background-image.png");
  background-size: cover;
  background-position: center;
  z-index: 0;
}

.homepage .container {
  width: 70%;
  margin-left: 15%;
  margin-right: 15%;
  height: 100%;
}

.homepage .container .row {
  width: 100%;
  height: 100%;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.homepage .container .row .col-50 {
  width: 50%;
  float: left;
}

.homepage .container .row .arrow-down {
  color: white;
  font-size: 2rem;
  width: 10%;
  margin-left: 45%;
}

@media only screen and (max-width: 468px) {
  .call {
    position: fixed;
    z-index: 999;
    right: 20px;
    bottom: 20px;
    width: 35%;
    padding: 8px 0px;
    border-radius: 10px;
    border: none;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.7), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.7), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    background: #010F23;
    font-size: 1.2rem;
    font-weight: 600;
  }
  .call a {
    color: #ffffff;
    text-decoration: none;
  }
  .homepage {
    width: 100%;
    background-image: url("../../background-image.png");
    background-size: cover;
    background-position: 600px;
    z-index: 0;
    overflow: hidden;
    padding-top: 13vh;
    margin-bottom: 0;
  }
  .homepage .container {
    width: 90%;
    margin-left: 5%;
    height: 100%;
  }
  .homepage .container .row {
    width: 100%;
    height: 100%;
  }
  .homepage .container .row .col-50 {
    width: 100%;
    background-size: red;
  }
  .homepage .container .row .col-50 img {
    width: 100%;
    margin-top: 10%;
  }
  .homepage .container .row .col-50 h1 {
    color: #FB2E55;
    margin-bottom: 0px;
  }
  .homepage .container .row .col-50 h1 span {
    color: white;
  }
  .homepage .container .row .col-50 h3 {
    color: white;
    margin-top: 0px;
  }
  .homepage .container .row .col-50 .purchase {
    background-image: -webkit-gradient(linear, left bottom, right top, from(#FD086D), color-stop(#FB3153), to(#FA5A3A));
    background-image: linear-gradient(to top right, #FD086D, #FB3153, #FA5A3A);
    color: white;
    font-size: 1.2rem;
    font-weight: 500;
    width: 65%;
    padding: 9px;
    margin-top: 2vh;
    border-style: none;
    border-radius: 5px;
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.7), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.7), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }
}
