@media only screen and (max-width:468px){
    .socialContact{
        background:#FB2E55;
        width:100%;
        height:100%;
        padding:30px 0px;
    
        .container{
            width:90%;
            margin-left:5%;
            text-align: center;
    
            h2{
                margin:0;
                padding:0;
                color:white;
                text-align:center;
                
                button{
                    background:#010F23;
                    color:white;
                    border:none;
                    color:white;
                    font-size:1.2rem;
                    font-weight:500;
                    width:65%;
                    padding:12px;
                    border-style:none;
                    border-radius:5px;
                    margin-top:25px;
                    box-shadow:0 4px 8px 0 rgba(0, 0, 0, 0.7), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    
    
                }
            }
            
            h3{
                color:white;
                margin:0;
                padding:0;
                text-align:center;
            }

            img{
                width:75px;
                height:75px;
                text-align: center;
                margin-top:4vh;
                color:white;
            }

           

        }
    }
}
